@media screen and (min-width: 601px) {
  div.example {
    font-size: 80px;
  }
}
@media screen and (max-width: 600px) {
  div.example {
    font-size: 30px;
  }
}
@media (max-width: 768px) {
  p {
      font-size: 10px;
      line-height: 0.5;
      margin-bottom: 1em;
  }
}
@viewport {
  width: device-width ;
  zoom: 1.0 ;
}
@-ms-viewport {
  width: device-width ;
}

.App{
  text-align: center;
  background-color:rgb(228, 228, 215); 
}
nav{
  display: flex;
  justify-content: space-around;
  min-height:10vh;
  background-color: rgb(9, 54, 70);
  color: white;
  font-weight: bold;
}
.navsub{

  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;

}

footer{
  display: flex;
  justify-content: space-around;
  min-height:10vh;
  background-color: rgb(9, 54, 70);
  color: white;
}
.image{
    width: 300px;
    max-width:100%;
    height: auto;
    list-style-position: inside;
    
}
.heading{
  display: flex;
  justify-content:center;
  font-size: 2.7vh;
  font-family: cursive ;
  margin:0 auto;
   width: 80%;
}


.headercov{
  color: red;
}

.socialmedia {
  background: #eee;
  padding: 25px 50px;
}
.social{
  margin:0 1vh;
  transition: transform 250ms;
  display: inline-block;
  

}
 .social:hover {
  transform: translateY(-2px);
}

.youtube {
  color: #eb3223;
}

.facebook {
  color: #4968ad;
}

.twitter {
  color: #49a1eb;
}

.instagram {
  color: black;
}

.github {
  color: black;
}
.india{
  color:white;
  font-weight: bold;
  font-size: 2.2vh;
  list-style-type: none;
}
.footstyle{
  font-size: 22px;
}
.update{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.matter{
  display: flex;
  justify-content: center;
  font-size: 2.1vh;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}